import React, { Component } from "react";
import {Box, Calendar, DropButton, DropButtonProps, MaskedInput} from 'grommet';
import {DateRange} from '../../../Types';
import {Masks, RangePointer} from '../../../constants';
import moment from 'moment';


type NewProps = Omit<DropButtonProps, "dropContent">;

export interface DropRangeCalendarProps extends NewProps {
    range: DateRange;
    onChange: ((event: changedEvent) => void);
    size?: "small" | "medium" | "large";
};

type changedEvent = {
    target: DropRangeCalendar,
    value: DateRange
}

const customRange: RangePointer = {
    name: "Custom", range: () => { return {} as DateRange; }
}


export default class DropRangeCalendar extends Component<DropRangeCalendarProps> {
    state = {
        start: this.props.range.start,
        szStart: moment(this.props.range.start).local().format("DD/MM/YYYY"),
        end: this.props.range.end,
        szEnd: moment(this.props.range.end).local().format("DD/MM/YYYY")
    };

    render() {
        return (
            <Box>
                <DropButton
                    dropContent={this.renderCalendars()}
                    {...this.props}
                />
            </Box>
        )
    }

    private renderCalendars = () => (
        <Box direction={"row"} pad={"small"}>
            {/*#region Start*/}
            <Box pad={"small"}>
                <Box direction="row" gap="2px" align="center" pad={{vertical: "small"}}>
                    <Box width="32px">
                        <i className="zwicon-calendar-month" style={{
                                color: "#9060EB"
                            }}
                        />
                    </Box>

                    <Box width={"160px"}>
                        <MaskedInput value={this.state.szStart} mask={Masks.date}
                             onChange={(event: any) => {
                                 const value = event.target.value;
                                 this.setState({szStart: value});
                             }} width={"180px"}
                             onBlur={(event: any) => {
                                 try {
                                     const m = moment(event.target.value, "DD/MM/YYYY");
                                     const start = m.toDate();

                                     this.setState({szStart: m.format("l"), start});

                                     if (this.props.onChange) {
                                         this.props.onChange({ target: this, value: { start, end: this.state.end } });
                                     }
                                 } catch {}
                             }}
                        />
                    </Box>
                </Box>

                <Calendar
                    color="secondary"
                    size={"small"}
                    date={this.state.start.toISOString()}
                    onSelect={
                        (d: any) => {
                            const m = moment(new Date(d));
                            const start = m.toDate();

                            this.setState({szStart: m.format("l"), start}, () => {
                                if (this.props.onChange) {
                                    this.props.onChange({ target: this, value: { start, end: this.state.end } });
                                }
                            });
                        }
                    }
                />
            </Box>
            {/*#endregion*/}

            {/*#region End*/}
            <Box pad={"small"}>
                <Box direction="row" gap="2px" align="center" pad={{vertical: "small"}}>
                    <Box width="32px">
                        <i className="zwicon-calendar-month" style={{
                            color: "#9060EB"
                        }}/>
                    </Box>

                    <Box width={"160px"}>
                        <MaskedInput value={this.state.szEnd} mask={Masks.date}
                                     onChange={(event: any) => {
                                         const value = event.target.value;
                                         this.setState({szEnd: value});
                                     }} width={"180px"}
                                     onBlur={(event: any) => {
                                         try {
                                             const m = moment(event.target.value, "DD/MM/YYYY");
                                             const end = m.toDate();

                                             this.setState({szEnd: m.format("l"), end});

                                             if (this.props.onChange) {
                                                 this.props.onChange({ target: this, value: { start: this.state.start as Date, end } });
                                             }
                                         } catch {}
                                     }}
                        />
                    </Box>
                </Box>

                <Calendar
                    color="secondary"
                    size={"small"}
                    date={this.state.end.toISOString()}
                    onSelect={
                        (d: any) => {
                            const m = moment(new Date(d));
                            const end = m.toDate();

                            this.setState({szEnd: m.format("l"), end}, () => {
                                if (this.props.onChange) {
                                    this.props.onChange({ target: this, value: { start: this.state.start as Date, end } });
                                }
                            });


                        }
                    }
                />
            </Box>
        {/*#endregion*/}
        </Box>
    )
}
