import React, { useContext, useState, useEffect } from 'react'
import { Box, Image, Heading, Text, ResponsiveContext, Chart, Select } from 'grommet';
import Hero from '../../Components/Hero/Hero';
import BackOfficeDashboardImage from "../../assets/images/dark-dashboard.jpg";
import styled from "styled-components";
import {DateRange, IPaymentChannel, IStats, ITransaction, FullTransaction, IUserAccount} from '../../Types';
import Axios, { AxiosError, AxiosResponse } from "axios";
import StatsBox from '../../Components/_BackOffice/StatsBox/StatsBox';
import moment from "moment";
import Scrollbars from 'react-custom-scrollbars';

const Wrapper = styled(Box)`
	width: 100vw;
	align-items: center;
	padding-bottom: 2rem;
	@media (min-width: 768px) {
		// align-items: start;
	}
`;

const Dashboard = () => {
	const size = useContext(ResponsiveContext);
	const [loading, setLoading] = useState(false);
	const [transactionData, setTransactionData] = useState({
        value: 0,
        previousValue: 0,
        percentageDifference: 0,
        transactions: [] as FullTransaction[]
    } as any);

	const updateCard = async (range: DateRange, url: string) =>  {
		try {
			const res = await Axios.get(url,
				{ params: range });
			console.log(res.data);
			return res.data;
		} catch(ex) {
			console.log(`An unexpected error has occured. ${ex}`);
		}
	}

	return (
		<Wrapper width="10vw" pad={{bottom: "2rem"}} align="center">
			<Hero
				image={BackOfficeDashboardImage}
				text="Dashboard"
			/>

			<Box justify="center" align="center" width="100%" height="100%">
				{/* <Box align="start" width="auto" height="600px">
					<Image src={ComingSoonImage} fit="contain"/>
				</Box>
				
				<Heading level="3">Hmmm...What's supposed to be here?</Heading>
				<Text>This page is still under construction, 
					updates will be shipping in a bit.
				</Text> */}

				<Box
					pad="xlarge"
					width="100%"
					height="100%"
					align="center"
					justify="center"
					margin={{top: "large", left: "medium"}}
					gap="large"
				>
					<Box direction="row" justify="start" width="100%" gap="xlarge">
						<StatsBox label="New Users"
								  onUpdate={(range: DateRange) =>
									  updateCard(range, "/api/backOffice/" +
										  "Overview/registrations/count")}
						/>

						<StatsBox label="Bets Insured"
								  onUpdate={(range: DateRange) =>
									  updateCard(range, "/api/backOffice/" +
										  "Overview/bets/count")}
						/>
					</Box>

					<Box direction="row" justify="start" width={"100%"} gap="xlarge">
						<StatsBox label="Bets Claimed"
								  onUpdate={(range: DateRange) =>
									  updateCard(range, "/api/backOffice/" +
										  "Overview/bets/claimed/count")}
						/>

						<StatsBox label="Transactions"
                                  style={{
                                      maxHeight: "800px"
                                  }}
								  height={"auto"}
								  onUpdate={async (range: DateRange) => {
									  let data = await updateCard(range, "/api/backOffice/" +
										  "Overview/transactions/count");

									  if (data) {
										  data.transactions = data.transactions.sort((a:FullTransaction, b:FullTransaction) => {
											  var dateA = new Date(a.addedAt), dateB = new Date(b.addedAt);
											  return dateA > dateB ? -1 : dateA < dateB ? 1 : 0;
										  });
                                          setTransactionData(data);
                                      }

									  return data;
								  }}
						>
                            <Box width={"100%"} height={"100%"} align={"stretch"} justify={"start"}>
                                <Box margin={{vertical: "30px"}} align={"center"} justify={"center"} width={"100%"}>
                                    <Text weight={"bold"} textAlign={"center"} size={"40px"}>N {transactionData.value.toLocaleString()}</Text>
                                </Box>

								{(transactionData.transactions && transactionData.transactions.length > 0) && (
									<Box width={"100%"} flex={"grow"}>
											<Box height={"100%"}>
												<Text size={"large"} weight={"bold"}>Recent Transactions</Text>
												<Box>
													<Scrollbars autoHide autoHeight
																autoHeightMax="500px"

													>
														<Box style={{minHeight:"100px"}}>
															{transactionData.transactions.map((t: FullTransaction, i: number) => (
																<Box className="hoverBar" key={i} gap="medium" direction="row" pad="small">
																	<Box width={"100px"}>
																		<Text size={"medium"} weight={"bold"} color={"#BAD23B"}>{t.amount}</Text>
																	</Box>

																	<Box width={"100px"}>
																		<Text size={"medium"} weight={"bold"}>{t.type}</Text>
																	</Box>

																	<Box width={"100px"}>
																		<Text size={"medium"} weight={"bold"}>
																			{moment(t.addedAt).format("lll")}
																		</Text>
																	</Box>

																	<Box>
																		<Text size={"medium"} weight={"bold"}>{t.auxilaryUser.username}</Text>
																	</Box>
																</Box>
															))}
														</Box>
													</Scrollbars>
												</Box>
											</Box>
									</Box>
								)}

                            </Box>

						</StatsBox>
					</Box>
				</Box>
			</Box>
			
		</Wrapper>
	)
}

export default Dashboard;
