import React, { Component } from "react";
import {Box, BoxProps, DropButtonProps, Select, Text} from 'grommet';
import {DateRange, IStats} from '../../../Types';
import moment from "moment";
import {DateRanges, Masks, RangePointer} from "../../../constants";
import Axios from 'axios';

import "./StatsBox.css";
import {JSXElement} from '@babel/types';
import DropRangeCalendar from '../../_Grommet/Selects/DropRangeCalendar';

type DivProps = Omit<JSX.IntrinsicElements['div'], "ref">;

type StatsBoxProps = {
    label: string;
    onUpdate?: (range: DateRange) => Promise<any>;
} & BoxProps & DivProps;

const customRange: RangePointer = {
    name: "Custom", range: () => { return {} as DateRange; }
}

export default class StatsBox extends Component<StatsBoxProps> {
    state = {
        busy: false,
        error: true,
        data: {value: 0, percentageDifference: 0} as any,
        currentRange: DateRanges[0],
    }

    componentDidMount(): void {
        this.setState({busy: true}, async () => {
            if (this.props.onUpdate) {
                const range = this.state.currentRange.range();
                this.setState({data: await this.props.onUpdate(range)},
                    () => {
                        this.setState({busy: false});
                    });
            }
        });
    }

    private update() {
        this.setState({busy: true}, async () => {
            if (this.props.onUpdate) {
                const range = this.state.currentRange.range();
                const data = await this.props.onUpdate(range);

                if (!data) {
                    this.setState({busy: false});
                }

                this.setState({data},
                    () => {
                        this.setState({busy: false});
                    });
            } else {
                this.setState({busy: false});
            }
        })
    }

    render() {
        return (
            <Box>
                <Box elevation="medium" width="500px" height="300px"
                     round="small" background="white" overflow="hidden"
                     {...this.props}
                >
                    <Box pad="medium" flex="grow">
                        <Box direction="row" justify="between" align="center" border={{
                            size: ".7px",
                            color: "black",
                            side: "bottom"}}
                             pad={{bottom: "small"}}
                        >
                            <Text weight="bold" size="large">{this.props.label}</Text>


                            <Box direction={"row"} align={"center"} justify={'center'} gap={"smal"}>
                                <Box width={"250px"}>
                                    <Select
                                        value={this.state.currentRange.name}
                                        options={DateRanges.map((d: RangePointer) => d.name)}
                                        onChange={(option) => {
                                            this.setState({currentRange: DateRanges[option.selected]},
                                                () => {
                                                    this.setState({busy: true}, async () => {
                                                        if (this.props.onUpdate) {
                                                            const range = this.state.currentRange.range();
                                                            this.setState({data: await this.props.onUpdate(range)},
                                                                () => {
                                                                    this.setState({busy: false});
                                                                });
                                                        }
                                                    })
                                                });
                                        }}
                                    />
                                </Box>

                                <DropRangeCalendar range={this.state.currentRange.range()}
                                   onChange={(event: any) => {
                                       const value: DateRange = event.value;
                                       const target: DropRangeCalendar = event.target;

                                       const strDate = moment(target.state.start).format("L") + " - " + moment(target.state.end).format("L");
                                       customRange.name = strDate;

                                       this.setState({busy: true, currentRange: customRange}, async () => {
                                           if (this.props.onUpdate) {
                                               const range = this.state.currentRange.range();
                                               this.setState({data: await this.props.onUpdate(range)},
                                                   () => {
                                                       this.setState({busy: false});
                                                   });
                                           }
                                       })
                                   }}
                                >
                                    <Box width="32px">
                                        <i className="zwicon-calendar-month" style={{
                                            color: "#9060EB"
                                        }}/>
                                    </Box>
                                </DropRangeCalendar>
                            </Box>
                        </Box>

                        <Box width="100%" flex="grow" pad={{vertical: "medium"}} gap="small" direction="row">
                            {this.props.children ? this.props.children : (
                                <>
                                    <Box border={{
                                        size: ".7px",
                                        color: "black",
                                        side: "right"}}
                                         height="100%"
                                         width="50%"
                                         align="center"
                                         justify="center"
                                         pad="medium"
                                    >
                                        <Text weight="bold" textAlign="center" size="50px">
                                            {this.state.data ? this.state.data.value : 0}
                                        </Text>
                                    </Box>

                                    <Box height="100%" width="50%"
                                         align="center"
                                         justify="center"
                                         pad="medium"
                                    >
                                        <Text weight="bold" textAlign="center" color={this.state.data && this.state.data.percentageDifference > 0 ? "#BAD23B" : "#d23b3b"} size="50px">
                                            {this.state.data ? (
                                                <>
                                                    {this.state.data.percentageDifference > 0 ? "+" : ""}
                                                    {this.state.data.percentageDifference}%
                                                </>
                                            ): "0%"}
                                        </Text>
                                    </Box>
                                </>
                            )}
                        </Box>

                    </Box>
                    <Box round="small" overflow="hidden">
                        <div className="load-bar">
                            {this.state.busy ? (
                                <>
                                    <div className="bar"></div>
                                    <div className="bar"></div>
                                    <div className="bar"></div>
                                </>
                            ): null}
                            <Box
                                onClick={(event: any) => this.update()}
                                className={"status"}
                                background={this.state.data && this.state.data.percentageDifference > 0 ? "#BAD23B" : "#d23b3b"}
                                height={"100%"}
                            />
                        </div>
                    </Box>
                </Box>
            </Box>
        );
    }
}
